
.gallery {
  padding: 40px;
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
  position: relative;
}

.thumbnails {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.thumbnail {
  flex: 1 0 33%;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

}

.frame {
  overflow: hidden;
}

.thumbnail img {
  width: 100%;
  height: 100%;
}


.single {
  overflow: hidden;
  height: 100vh;
}

.single img {
  max-width: 100%;
  max-height: 100vh;
}

.back {
  position: fixed;
  top: 50px;
  right: 50px;
  font-size: 54px;
  z-index: 1;
}

.back a {
  text-decoration: none;
}


